import './maps.css';

import galaxy from '../../assets/maps/galaxy.jpeg';
import closeup from '../../assets/maps/overlay.png';
import crosshairs from '../../assets/crosshairs.svg';

function Planet(props) {
  const { name, type, affiliation, climate, pop, src } = props.planet;
  const system = props.planet.system || props.system;
  return (
    <section className="planet-section">
      <StyledSubheader>{name}</StyledSubheader>
      <StyledSubheader4>{affiliation}</StyledSubheader4>
      <StyledSubheader3>{type}</StyledSubheader3>
      <img src={src} alt={name}></img>
      <StyledParagraph>Population: {pop}</StyledParagraph>
      <StyledParagraph>Climate: {climate}</StyledParagraph>
      <StyledParagraph>System: {system}</StyledParagraph>
    </section>
  );
}

function System(props) {
  const { name, planets } = props.system;
  return (
    <section className="system-section">
      <StyledHeader>{name} System</StyledHeader>
      <ul>
        {planets.map(function (planet, index) {
          return (
            <li key={`planet-${index}`}>
              <Planet planet={planet} system={name} />
            </li>
          );
        })}
      </ul>
    </section>
  );
}

