import { StyledHeader, StyledSubheader, StyledSubheader4 } from '../styled';
import { leaders, agents, artifacts } from './characterData';
import './characters.css'

function Character(props) {
  const { name, title, src, description, emblem } = props.character;
  return (
    <section className="character-section">
      <aside>
        <img src={src} alt={name}></img>
      </aside>
      <article>
        <img className="background-emblem" src={emblem} />
        <StyledSubheader>{name}</StyledSubheader>
        <StyledSubheader4>{title}</StyledSubheader4>
        <p>{description}</p>
      </article>
    </section>
  );
}

function Characters() {
  return (
    <>
      <StyledHeader>Leaders of Renown</StyledHeader>
      <ul className="char-list">
        {leaders.map(function (character, index) {
          return (
            <li key={`leader-${index}`}>
              <Character character={character} />
            </li>
          );
        })}
      </ul>

      <StyledHeader>Agents of Note</StyledHeader>
      <ul className="char-list">
        {agents.map(function (character, index) {
          return (
            <li key={`agent-${index}`}>
              <Character character={character} />
            </li>
          );
        })}
      </ul>

      <StyledHeader>Artifacts of Distinction</StyledHeader>
      <ul className="char-list">
        {artifacts.map(function (character, index) {
          return (
            <li key={`artifact-${index}`}>
              <Character character={character} />
            </li>
          );
        })}
      </ul>
    </>
  );
}

export default Characters;